main {

    section {
        min-height: 100dvh;
    }

    section#technologies {
        background: #ddd;

        max-width: 100vw;

        .tech {
            padding: 1rem;

            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
            gap: 2rem;

            .language-box {
                background-color: white;
                border: 1px solid #ddd;
                border-radius: 8px;
                padding: 2rem 1.5rem;
                text-align: center;
                box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
                transition: transform 0.3s ease, box-shadow 0.3s ease;

                text-decoration: none;
                cursor: pointer;

                &:hover {
                    transform: translateY(-5px);
                    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
                }

                img {
                    width: 100px;
                    object-fit: cover;
                    margin-bottom: 10px;
                }

                h3 {
                    margin: 0;
                    margin-top: 1rem;
                    font-size: 18px;
                }

            }
        }

    }

    section#resources {
        padding: 1.5rem;
        margin-top: 100px;

        h1 {
            display: flex;
            gap: .5rem;
        }


        ul {
            list-style: none;

            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
            padding: 0;
            gap: 1.5rem;

            li {
                background: rgb(233, 237, 237);
                padding: 1rem;
                border-radius: 10px;
                transition: .2s all;

                box-shadow: 2px 2px 7px #313f626b;


                &:hover {
                    scale: 1.05;
                    background: rgb(193, 229, 255);
                }

                a {
                    text-decoration: none;
                    color: #000;
                    display: flex;
                    flex-direction: column;
                    gap: 1rem;

                    h3 {
                        margin: 0;
                        color: #8f8f8f;
                        font-size: 1rem;
                    }

                    img.thumbnail {
                        width: 100%;
                        border-radius: 10px;
                    }


                    .author_info {

                        display: flex;
                        gap: .5rem;
                        align-items: center;

                        img {
                            border-radius: 50%;
                            width: 30px;
                            height: 30px;
                        }

                    }

                }
            }
        }
    }
}






/* Hero Section */

section.hero {
    position: relative;
    top: 0;
    text-align: center;

    background: radial-gradient(#2575fc, #7b00ff);
    background: url("https://tse2.mm.bing.net/th?id=OIG4.gZw6M0.leGwHGq29pKo_&pid=ImgGn");
    background: url("https://tse2.mm.bing.net/th?id=OIG1.EapVr78nRZhw2dovtg9L&pid=ImgGn");
    background-size: 125%;
    // background-position: center;

    transition: 0s;

    color: #fff;
    padding: 0;

    height: 100dvh;
    max-height: 100dvh;
    z-index: 1;

    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        // background: radial-gradient(#2574fc8d, #5b23f4ce, #2641c8);
        background: radial-gradient(#25d8fc8d, #35265fce, #29325b);
        // background: radial-gradient(#fbff1e5c, #a27131ce, #c8266c);

        width: 100%;
        height: 100%;

        padding: 1rem;

        h1 {
            font-size: 2.5rem;
            margin-bottom: 10px;

            filter: drop-shadow(2px 2px 2px #00000055);
        }

        p {
            font-size: 1.2rem;
            margin-bottom: 20px;

            filter: drop-shadow(2px 2px 2px #00000055);
        }

        .cta-button {
            background: #fff;
            color: #2575fc;
            padding: 10px 20px;
            border-radius: 20px;
            font-size: 1rem;
            text-decoration: none;

            box-shadow: 0px 0px 10px #00f2ff;

            &:hover {
                box-shadow: 0px 0px 20px 2px #8bf9ff;
            }
        }
    }
}



@media screen and (width < 800px) {

    section.hero {
        .container {
            h1{
                font-size: 1.9rem;
            }
        }
    }
    
}